body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.noselect {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  /* Konqueror HTML */
  /* Old versions of Firefox */
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                    supported by Chrome, Edge, Opera and Firefox */
}

.container {
  background-image: url(/cairosmoke/static/media/lightningBackground.127e729d.jpg);
  background-attachment: scroll;
  width: 100%;
  min-height: 100vh;
  height: -webkit-max-content;
  height: max-content;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

@media (max-width: 1025px) {
  .container {
    background-position: center center;
  }
}

.static-container {
  margin: 0 auto;
  width: 100vw;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.cairo__description {
  display: flex;
  width: calc(100% - 1000px);
  text-shadow: 0 0 50px #989494;
  margin-left: 2vw;
  margin-bottom: 10px;
  font-family: Ibarra;
  font-size: 1.5rem;
  text-transform: lowercase;
}

@media (max-width: 420px) {
  .cairo__description {
    width: calc(100vw - 10px);
    font-size: 1.1rem;
    text-shadow: 0 0 20px #989494;
  }
}

@media (min-width: 421px) and (max-width: 628px) {
  .cairo__description {
    text-shadow: 0 0 20px #989494;
    width: calc(100% - 100px);
    font-size: 1.2rem;
  }
}

@media (min-width: 629px) and (max-width: 1025px) {
  .cairo__description {
    text-shadow: 0 0 50px #989494;
    width: calc(100% - 300px);
  }
}

.dropdown_height {
  min-height: -webkit-fit-content;
  min-height: -moz-fit-content;
  min-height: fit-content;
  transition: height 3s ease;
}

.dropdown__body {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.dropdown__item {
  margin: 0 0 10px 0;
  color: white;
  font-family: "Ibarra";
  font-size: 1.7rem;
  list-style: none;
  text-transform: uppercase;
  cursor: pointer;
  transition: font-family 5s ease;
}

.dropdown__item:hover {
  font-family: "Ibarra hover";
}

.cairo__container {
  width: 25%;
  height: 25%;
  margin-top: 30vh;
  position: absolute;
}

@media (max-width: 420px) {
  .cairo__container {
    margin-top: 20vh;
    width: 85vw;
  }
}

@media (min-width: 421px) and (max-width: 628px) {
  .cairo__container {
    margin-top: 20vh;
    width: 65vw;
  }
}

@media (min-width: 629px) and (max-width: 1025px) {
  .cairo__container {
    margin-top: 20vh;
    width: 35vw;
  }
}

.cairo__lightning {
  width: 100%;
  -webkit-animation: lightning 3s linear;
          animation: lightning 3s linear;
  opacity: 1;
}

@-webkit-keyframes lightning {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 0;
  }
  11% {
    opacity: 1;
  }
  17% {
    opacity: 0;
  }
  30% {
    opacity: 0;
  }
  31% {
    opacity: 1;
  }
  37% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@keyframes lightning {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 0;
  }
  11% {
    opacity: 1;
  }
  17% {
    opacity: 0;
  }
  30% {
    opacity: 0;
  }
  31% {
    opacity: 1;
  }
  37% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

